import React, {useState} from "react";
import { NavLink } from "react-router-dom";

const Contact = () =>{

const [fullName, SetFullName] = useState({
fname: '',
lname: ''
});
const [fullName1, SetFullName1] = useState({
fname: '',
lname: ''
});
const Events = (e) => {
const names = e.target.name;
const valu = e.target.value;

        SetFullName((preValue) => {
            return{
                ...preValue,
                [names]: valu
            };

        });
}

    const onSubmits = (e) => {
    e.preventDefault();
    SetFullName1(fullName);

    }
return(<>
<form onSubmit={onSubmits} >
<h1>contacr us form heare  </h1>
<h3>{fullName.fname}  {fullName.lname}</h3>
<input type="text"
    onChange={Events}
    placeholder="enter your first name"
    name="fname"
    value={fullName.fname}
/>
<input type="text"
    onChange={Events}
    placeholder="enter your last name"
    name="lname"
    value={fullName.lname}
/>
<button type="submit">Submit</button>
<div><NavLink exact to="/new-contact">New Form</NavLink></div>
<div><h1>{fullName1.fname}</h1>
<h1>{fullName1.lname}</h1></div>
</form>
</>
);
}
export default Contact;